.new-collection{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
    margin-top: 207px;
    }
    .new-collection h1{
        color: #171717;
        font-size: 50px;
        font-weight: 600;
    }
    .new-collection hr{
        width: 200px;
        height: 6px;
        border-radius: 10px;
        background: #252525;
    }
    .collection{
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      margin-top: 50px;
      gap: 30px;
    }