.loginsignup{
  width: 100%;
  height: 114vh;
  padding-top: 100px;
  background: #fce3fe;
}
.loginsignup-container {
    width: 412px;
    height: 534px;
    background: white;
    margin: auto;
    padding: 40px 60px;
}
.loginsignup-container h1{
    margin: 10px 0;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 30px;
    
}
.loginsignup-fields input{
    height: 72px;
    
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}
.loginsignup-container  button{
    width: 412px;
    height: 72px;
    color: white;
    background: #ff4141;
    margin-top: 30px;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}
.loginsignup-login{
margin-top: 5px;
color: #5c5c5c;
font-size: 18px;
font-weight: 500;
}
.loginsignup-login span{
    color: #ff4141;
    font-weight: 600;
}
.loginsignup-agree{
    display: flex;
    align-items: center;
    
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}