.cartitem{
    
    margin: 25px 170px;
}
.cartitem hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}
.cartitem-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    
    padding: 20px 0;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitem-format {
    font-size: 17px;
    font-weight: 500;
}
.carticon-producticon{
    height: 62px;
}
.carticon-remove{
    width: 15px;
    margin: 0 40px;
    cursor: pointer;
}
.cartitem-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: white;
}
.cartitem-down{
    display: flex;
    margin: 100px 0;
}
.cartitem-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 20px;
}
.cartitem-totalitem{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.cartitem-total button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background : #ff5a5a;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.cartitem-promocode{
    flex: 1;
    font-size: 15px;
    font-weight: 500;
}
.cartitem-promocode p{
    color: #454545;
}
.cartitem-promobox{
    width: 504px;
    margin-top: 15px;
    padding-left: 15px;
    height: 58px;
    background: #eaeaea;

}
.cartitem-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.cartitem-promobox button{
    width: 170px;
    height: 58px;
    font-size: 15px;
    background: black;
    color: white;
    cursor: pointer;
}